<template>
    <div class="container p-0">
        <div class="card">
            <textarea id="sql-editor" class="form-control" cols="30" rows="10"></textarea>
            <button class="btn btn-light btn-flat btn-no-radius">Execute</button>
        </div>
    </div>
</template>

<script type="text/ecmascript-6">
  const CodeMirror = require("codemirror/lib/codemirror");

  require("codemirror/lib/codemirror.css");
  require("codemirror/mode/sql/sql");
  require("codemirror/addon/hint/show-hint.css");
  require("codemirror/addon/hint/show-hint");
  require("codemirror/addon/hint/sql-hint");

  export default {
    name: "Sql",

    data() {
      return {
        configs: {
          sqlEditor: {
            lineNumbers: true,
            smartIndent: true,
            indentWithTabs: true,
            extraKeys: {"Ctrl-Space": "autocomplete"},
            mode: "text/x-pgsql",
            matchBrackets: true,
            autofocus: true,
          },
        },

        codeMirror: {},
      };
    },

    mounted() {
      this.codeMirror = CodeMirror.fromTextArea(
          document.getElementById("sql-editor"), this.configs.sqlEditor,
      );
    },
  };
</script>

<style scoped>

</style>



